import logo from './logo.svg';
import './App.scss';
import { Card, Button, Fade, ButtonGroup, Form, Alert, InputGroup, FormControl, Accordion } from 'react-bootstrap'
// import 'bootstrap/dist/css/bootstrap.min.css';
import WinePlate from './components/WinePlate';
import React, { useEffect, useState } from 'react';
import axios from 'axios'
import * as Icon from 'react-bootstrap-icons';

const MAIN_COLOR = '#e7154e'
const SECONDARY_COLOR = '#fce3ee'

const courses = [
  "appetizer",
  "main",
  "dessert"
]

const types = {
  'red': '#731624',
  'white': '#fadb7d',
  'sparkling': '#305432',
  'rose': '#e0c8df',
  'desert': '#adbdd9'
}

const pairs = ['grilled food', 'pork', 'mutton, lamb', 'beef', 'savoury snacks',
  'casseroles', 'chicken, turkey', 'lean fish',
  'salads, vegetarian food', 'pasta and pizza', 'party wine',
  'oriental food', 'buffet', 'strong cheeses', 'game', 'fatty fish',
  'mild cheeses', 'spicy and meaty sausages', 'sushi',
  'mild sausages', 'tapas and antipasti', 'berries and fruits',
  'hot food', 'seafood', 'blini', 'sweet desserts',
  'clams, mussels, scallops and oysters', 'chocolate desserts',
  'mushrooms', 'soups', 'liver', 'game birds']

function App() {
  const [selected, setSelected] = useState('')
  const [meal, setMeal] = useState('')
  const [pair, setPair] = useState('')
  const [recipe, setRecipe] = useState({})
  const [wining, setWining] = useState(false)
  const [advSearch, setAdvSearch] = useState(false)
  const [allWines, setAllWines] = useState([])
  const [displayedWines, setDisplayedWines] = useState([])
  const [searchErr, setSearchErr] = useState('')
  const [width, setWidth] = useState(window.innerWidth);
  const [priceRange, setPriceRange] = useState('$$')
  const [searchVisible, setSearchVisible] = useState(true)


  const [filterWhite, setFilterWhite] = useState(true)
  const [filterRed, setFilterRed] = useState(true)
  const [filterSparkling, setFilterSparkling] = useState(true)
  const [filterRose, setFilterRose] = useState(true)
  const [filterDessert, setFilterDessert] = useState(true)

  useEffect(() => {
    window.addEventListener('resize', handleWindowSizeChange);
    return () => {
      window.removeEventListener('resize', handleWindowSizeChange);
    }
  }, [])

  let isMobile = (width <= 768);
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty('--vh', `${vh}px`);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }

  const getAdvSearchHeight = () => {
    if (advSearch) {
      return 100
    }
    return 30
  }

  const categorySpan = () => {
    if (courses.includes(selected)) {
      return {
        'appetizer': 'appetizer',
        'main': 'main course',
        'dessert': 'dessert'
      }[selected]
    }
    return 'select a course'
  }

  const handleSelect = (course) => {
    if (selected === course) {
      setSelected('')
    } else {
      setSelected(course)
    }
  }

  const priceIndex = (level) => {
    return {
      '$': 0,
      '$$': 1,
      '$$$': 2
    }[level]
  }

  const handlePriceLevelChange = (level) => {
    setPriceRange(level)
    setDisplayedWines(allWines[priceIndex(level)])
    // setWining(true)

    // const IMAGES = allWines[priceIndex(level)].map(w => imageLink(w.link))
    // Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
    //   .then(() => {
    //     setDisplayedWines(allWines[priceIndex(level)])
    //     setWining(false)
    //   })
    //   .catch(err => console.log("Failed to load images", err))
  }

  const handleWineMe = e => {
    e.preventDefault()
    setPair(meal)
    setWining(true)

    const options = {
      method: 'POST',
      url: 'https://pocketsomm.dev/api/v1/link2wine',
      headers: { 'Content-Type': 'application/json' },
      data: { link: meal }
    };

    axios.request(options).then(function (response) {
      console.log(response.data)
      const wines = response.data.wines

      setRecipe(response.data.recipe)
      setAllWines(
        wines
      )
      setDisplayedWines(wines[priceIndex(priceRange)])
      setSearchErr(false)

      const IMAGES = wines.flat().map(w => imageLink(w.link))
      Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
        .then(() => {
          setWining(false)
          setSearchVisible(false)
        })
        .catch(err => console.log("Failed to load images", err))

    }).catch(function (error) {
      console.error(error);
    });

    // const wines = rows.filter(e => e.pair && e.pair.toUpperCase() === meal.toUpperCase())
    // setDisplayedWines(
    //   wines
    // )

    // if (wines.length == 0)
    //   setTimeout(
    //     function () {
    //       if (wines.length == 0)
    //         setSearchErr(`"${meal}" is not a supported dish.\n Try searching "${pairs[Math.floor(Math.random() * pairs.length)]}" instead`)
    //       else
    //         setSearchErr(false)
    //       setWining(false)
    //     },
    //     1000
    //   )
    // else {
    //   setSearchErr(false)
    //   const IMAGES = wines.map(w => imageLink(w.link))

    //   Promise.all(IMAGES.map(image => image ? loadImage(image) : null))
    //     .then(() => {
    //       setWining(false)
    //     })
    //     .catch(err => console.log("Failed to load images", err))
    // }
  }

  const imageLink = (link) => {
    let productNumber = link.split('/')[5]
    let productName = link.split('/')[6]
    return `https://images.alko.fi/images/cs_srgb,f_auto,t_products/cdn/${productNumber}/${productName}.jpg`
  }

  function textIsLink() {
    var expression = /[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)?/gi;
    var regex = new RegExp(expression)

    if (meal.match(regex)) {
      return true
    } else
      return false
  }

  const loadImage = image => {
    return new Promise((resolve, reject) => {
      const loadImg = new Image()
      loadImg.src = image
      // wait 2 seconds to simulate loading time
      loadImg.onload = () =>
        setTimeout(() => {
          resolve(image)
        }, 2000)

      loadImg.onerror = err => reject(err)
    })
  }

  function WineDisplay() {
    return (
      <div
        className="no-scrollbar"
        id="wine-display"
        style={{ width: '100%' }}>
        {
          displayedWines.map(wine =>
            WinePlate(wine)
          )
        }
      </div>
    )
  }

  const winelistHeight = () => {
    if (wining)
      return '0px'
    else if (pair) {
      return isMobile ? `calc(100vh - ${80 + 80 + 2 * 16 + 67 + 50}px)` : '500px'
    }
    else
      return '0px'
    // (pair && !wining && !searchErr) ? isMobile ? `calc(100vh - ${80 + 80 + 2 * 16 + 67 + 50}px)` : '500px' : '0px'
  }

  return (
    <div className="App">
      <Form onSubmit={e => { handleWineMe() }}>
        <Card className="mainBody">
          <Card.Body>
            <div style={{ height: '80px' }}>
              <img onClick={e => { 
                setPair(''); 
                setSearchVisible(true); 
                setPriceRange('$$');
                setMeal('')
                document.getElementById(
                  'inlineFormInputGroup').value = ''
                }} className="clickable nodrag" src="/Roznavene-project-demo/logo.svg" width="200px"></img>
              {/* <b style={{ fontSize: '23px' }}>PocketSomm</b><br /> */}
            </div>
            <div style={{ width: '80%', margin: 'auto', textAlign: 'start' }}>
              <Form.Group style={{ height: '80px' }}>
                {/* <Fade in={!recipe['title'] || wining}> */}
                <Form.Label style={{
                  textAlign: 'start',
                  fontSize: '14px'
                }}>
                  WHAT ARE YOU EATING?
                </Form.Label>
                {/* </Fade> */}
                {/* <Form.Label style={{ width: '79%', textAlign: 'start', fontSize: recipe['title'] ? '16px' : '14px' }}>
                {recipe['title'] ? <b>{recipe['title'].toUpperCase()}</b> : 'WHAT ARE YOU EATING?'}
              </Form.Label> */}

                <Fade in={!searchVisible}>
                  <div style={{ display: !searchVisible ? '' : 'none' }}>
                    <Form.Label style={{
                      textAlign: 'start',
                      fontSize: '16px'
                    }}>
                      <b>{recipe['title'] ? recipe['title'].toUpperCase() : ''}</b>
                    </Form.Label>
                    <Icon.X 
                      className="clickable nodrag" 
                      style={{marginLeft:'5px'}} 
                      size={20} 
                      color='gray' 
                      onClick={e=>{setSearchVisible(true)}}/>
                  </div>
                </Fade>

                <Fade in={searchVisible}>
                  <InputGroup style={{
                    display: searchVisible ? '' : 'none',
                    margin: 'auto'
                  }} >
                    <InputGroup.Text>🍽️</InputGroup.Text>
                    <FormControl onChange={e => setMeal(e.target.value)} id="inlineFormInputGroup" placeholder="enter recipe link" />
                  </InputGroup>
                </Fade>
                {
                  (!textIsLink() && meal) ? <div style={{ width: '80%', margin: 'auto' }}>
                    <span style={{ color: 'darkred', float: 'left', fontSize: '12px', margin: '2px' }}>
                      * corked link
                    </span>
                  </div> : null
                }
              </Form.Group>
            </div>
            {
              searchErr ? <Alert style={{ width: '80%', margin: 'auto' }} variant="danger">{searchErr}</Alert> : null
            }
            {/* {
              pair && !wining ? <div className="animated" style={{ width: '80%', margin: 'auto', height: `${getAdvSearchHeight()}px` }}>
                <a style={{ color: 'gray', fontSize: '13px' }} href="#" onClick={e => { setAdvSearch(!advSearch) }}>Adjust preference</a>
                <Fade in={advSearch}>
                  <div style={{ width: '90%', margin: 'auto' }}>
                    <h4 style={{ textAlign: 'left', fontSize: '20px' }}>Wine types</h4>
                    <ButtonGroup size="sm" className="mt-3">
                      <Button onClick={e => { setFilterWhite(!filterWhite) }} active={filterWhite} variant="outline-danger">White</Button>
                      <Button onClick={e => { setFilterRed(!filterRed) }} active={filterRed} variant="outline-danger">Red</Button>
                      <Button onClick={e => { setFilterSparkling(!filterSparkling) }} active={filterSparkling} variant="outline-danger">Sparkling</Button>
                      <Button onClick={e => { setFilterRose(!filterRose) }} active={filterRose} variant="outline-danger">Rose</Button>
                      <Button onClick={e => { setFilterDessert(!filterDessert) }} active={filterDessert} variant="outline-danger">Dessert</Button>
                    </ButtonGroup>
                  </div>
                </Fade>
              </div> : null
            } */}
            <Fade in={!wining && pair}>
              <ButtonGroup aria-label="Basic example" style={{ 
                marginBottom: '7px', 
                display: !wining && pair ? '' : 'none' }}>
                {
                  ["$", "$$", "$$$"].map(level =>
                    <Button
                      key={level}
                      onClick={e => {
                        handlePriceLevelChange(level)
                      }}
                      size="sm"
                      variant="outline-danger"
                      active={priceRange == level}
                      style={{ width: '45px', height: '30px' }}
                    >{level}
                    </Button>
                  )
                }
              </ButtonGroup>
            </Fade>
            {/* {
              !wining && pair ?
                <div style={{ height: '50px', width: '100%' }}>
                  <h4>Wines for {recipe['title']}</h4>
                </div> : null
            } */}
            <div
              id="winelist"
              className="animated winelist"
              style={{
                width: '100%',
                margin: 'auto',
                height: winelistHeight(),
                // height: (pair && !wining && !searchErr) ? isMobile ? `calc(100vh - ${80 + 80 + 2 * 16 + 67 + 50}px)` : '500px' : '0px',
                // maxHeight: (pair && !wining && !searchErr) ? `${620 - getAdvSearchHeight()}px` : '0px',
                overflowY: 'auto',
                overflowX: 'hidden'
              }}>
              {/* <Fade in={wining}>
                <img
                  src="/Roznavene-project-demo/swirling.gif"
                  width="150px"
                  style={{
                    marginTop: '20px',
                    position: 'absolute',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    left: 0,
                    right: 0
                  }}
                />
              </Fade> */}
              {
                ['$', '$$', '$$$'].map(e =>
                  <Fade key={`${e}_display`} in={!wining && priceRange === e}>
                    <div style={{ display: wining || priceRange !== e ? 'none' : '' }}>
                      <WineDisplay />
                    </div>
                  </Fade>
                )
              }
            </div>
          </Card.Body>
          <Card.Footer className="button-footer">
            <Button
              type="submit"
              disabled={!meal || wining || !textIsLink()}
              onClick={handleWineMe}
              variant="danger"
              style={{ width: '95%', height: '50px', position: 'relative', zIndex: 0 }}>
              <span>
                {
                  wining ? <div>
                    <div className="rotate-center" style={{ display: 'inline-flex', marginRight: '8px' }}>🍷</div><span>PAIRING...</span>
                  </div>
                    : 'WINE ME'
                }
              </span>
              {/* {
                wining ? <div>
                  <span>🍷</span>
                  <span>PAIRING...</span>
                </div> : <span>WINE ME</span>
              } */}
              {/* <Loadable loading={wining} component={<span>WINE ME </span>} /> */}
            </Button>
          </Card.Footer>
        </Card>
      </Form>
      {/* <div id="foot" style={{ position: 'relative', height: '5px' }}>
          <span style={{ right: '0px', position: 'absolute', fontSize: '13px', color: 'gray' }}>powered by PocketSomm™</span>
        </div> */}
    </div>
  );
}

export default App;
